.backdrop {
  width: 100%;
  height: 100%;
  z-index: 1000;
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  background: rgba(0, 0, 0, .2);
  transition: opacity .25s, visibility .25s;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  width: 280px;
  height: 940px;
  background-color: #fff;
  border-radius: 4px;
  padding: 48px 20px;
  position: absolute;
  top: 50%;
