/* ============= Current ============= */

a.current-link {
  color: var(--accent-color);
}

.current-link {
  position: relative;

  @media screen and (min-width: 768px) {
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 1;

      border-radius: 2px;
      display: block;
      width: 100%;
      height: 4px;
      background-color: var(--accent-color);
    }
  }
}
