body.light-theme {
    color: var(--dominant-color);

}

body.dark-theme {
    background-image:
        linear-gradient(to right, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)),
        url('/src/images/header-background-desktop-1x.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

}

.checkbox {
    cursor: pointer;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 2;
    border: none;
    border-radius: 50px;
}

#switch {
    margin: auto;
    width: 100%;
    position: relative;
    display: block;
    margin-right: 10px;
    bottom: 15px;
    width: 60px;
    height: 25px;
    border-radius: 50px;
    font-size: 14px;
    font-weight: 700;
    background-color: #ffffff;
    box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
    /* Firefox */
    -moz-transition: all 0.5s ease-in;
    /* WebKit */
    -webkit-transition: all 0.5s ease-in;
    /* Opera */
    -o-transition: all 0.5s ease-in;
    /* Standard */
    transition: all 0.5s ease-in;
}

#switch.checkLight {
    background-color: rgba(51, 51, 51, 1);
}

#switch .icon {
    position: absolute;
    top: 5px;
    left: 7px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #E6A555;
    transition: left 0.4s ease-out;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}

#switch:after {
    position: absolute;
    left: 30px;
    top: -7px;
    color: rgba(51, 51, 51, 1);
    opacity: 1;
    content: 'Day';
    text-align: right;
    line-height: 40px;
    font-size: 12px;

}

#switch.checkLight .icon {
    position: absolute;
    top: 5px;
    left: 38px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 1);
}

#switch.checkLight .icon::before {
    position: absolute;
    top: 2px;
    left: -2px;
    opacity: 1;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: rgba(51, 51, 51, 1);
    content: '';


}

@keyframes leftTo {
    0% {
        left: -20px;
        opacity: 0;
    }

    50% {
        left: -15px;
        opacity: 0;
    }

    100% {
        left: -4px;
        opacity: 1;
    }
}

#switch.checkLight::before {
    position: absolute;
    left: 6px;
    top: -7px;
    color: #fff;
    content: 'Night';
    font-size: 11px;
    text-align: left;
    line-height: 40px;
    opacity: 1;


}

#switch.checkLight::after,
#switch::before {
    content: '';
    opacity: 0;
}